import { Component, ViewChild } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';

import { TranslateService } from '@ngx-translate/core';
import { ImageProvider } from '../../../providers/image/image';
import { Camera } from '@ionic-native/camera';
import { Storage } from '@ionic/storage';
import { SafeResourceUrl } from '@angular/platform-browser';

@IonicPage()
@Component({
  selector: 'page-eventos-create',
  templateUrl: 'eventos-create.html',
})
export class EventosCreatePage {
  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public fechaini		     = (new Date()).toISOString();
  public imagen		     : any;
  public fechafin		    = (new Date()).toISOString();
  public ubicacion		     : any;
  public acceso		     : any;
  plantilla2:any;
  vplantilla: any=[];
  public unidad		     : any;
  public fecha		     : any;
  foto1img: SafeResourceUrl;
  pofilel: string = './assets/img/photo256.png';
  public impresion		     : any;
  public nomax		     : any;
  public costo		     : any;
  plantilla:any;
  private _ID                  : String;
  public foto              	: any;
  public pageTitle           	: string;
  public image              	: any;
  idempresa:any;

  isDisablednit :any;
  constructor(public storage: Storage, private _IMAGE       : ImageProvider, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {

     this.form = this._FB.group({
        nombre     : ['', Validators.required],
        fechaini     : ['', Validators.required],
        fechafin     : ['', Validators.required],
        fecha     : [''],
        plantilla     : [''],
        ubicacion     : ['', Validators.required],
        impresion     : ['', Validators.required],
        nomax     : ['', Validators.required],
        costo     : ['', Validators.required],
        imagen     : ['']
     });




     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value




  })

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }




  onSelectChange(selectedValue: any) {
    this.plantilla2=selectedValue;


     // this.myInput2.setFocus();
      //this.focused('a')

  }
  ionViewDidLoad() : void
  {

    this.acceso = this.navParams.data.acceso;
    this.unidad=this.acceso
    if(this.acceso=='ADMINISTRADOR')
    {

      this.unidad='CURSOSLIBRES'

    }

    if(this.navParams.get("record")==null)
    {
      this.authService.getReg(this.idempresa+ '/' +  this.unidad + '/todos','catalogodiplomas').then((data) => {
        this.vplantilla=data;
      }, (err) => {
      this.msj.showError3('Error',err.error);
    });
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });

      this.isDisablednit =true
    }
    else {

       this._ID 				=	this.navParams.data.record._id;

       this.nombre		=	this.navParams.data.record.nombre;
       this.fechaini		=	this.navParams.data.record.fechaini;

       this.fechafin		=	this.navParams.data.record.fechafin;
       this.fecha		=	this.navParams.data.record.fecha;
       this.ubicacion		=	this.navParams.data.record.ubicacion;
       this.costo		=	this.navParams.data.record.costo;
       this.authService.getReg(this.idempresa+ '/' +  this.unidad + '/todos' ,'catalogodiplomas').then((data) => {
        this.vplantilla=data;
    //    this.vplantilla.push({_id:'na',nombre:'NO APLICA'})
        this.plantilla		=	this.navParams.data.record.plantilla;
      }, (err) => {
      this.msj.showError3('Error',err.error);
    });


       this.authService.convertToDataURLviaCanvas(this.navParams.data.record.foto, 'image/jpeg').
       then( base64 => this.foto1img = base64);


       this.image	=	this.navParams.data.record.foto;
       this.imagen 	=	this.navParams.data.record.foto;
       this.impresion		=	this.navParams.data.record.impresion;
       this.nomax		=	this.navParams.data.record.nomax;
       this.unidad	=	this.navParams.data.record.unidad;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });


    }
  }


formatAMPM(date) {
  var hours = date[0];
  var minutes = date[1];
  var ampm = hours >= 12 ? 'pm' : 'am';
  hours = hours % 12;
  hours = hours ? hours : 12;
  minutes = minutes < 10 ? '0'+minutes : minutes;
  var strTime = hours + ':' + minutes + ' ' + ampm;
  return strTime;
}





  submitForm0() {
    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

    this.authService.subefoto('',  'Images',  this.foto1img, '' , '' , '', '' , 'create' ).then((result) => {
      let data: any = [];
      data = result;
      this.form.controls['imagen'].setValue(data.url);

          const   options: any	     = {
            idempresa:this.idempresa,
            nombre : this.form.controls['nombre'].value,
            fechaini : this.form.controls['fechaini'].value,
            fechafin : this.form.controls['fechafin'].value,
            ubicacion : this.form.controls['ubicacion'].value,
            fecha : this.form.controls['fecha'].value,
            costo : this.form.controls['costo'].value,
            foto : this.form.controls['imagen'].value,
            plantilla : this.form.controls['plantilla'].value,
            impresion : this.form.controls['impresion'].value,
            nomax : this.form.controls['nomax'].value,
            unidad: this.unidad,
            bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' evento '}
        };

          if (options) {
            this.authService.createReg('', options, 'eventos').then((result) => {
              if (!this.form.valid) { return; }
              console.log(result)
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });

          }

    }, (err) => {
      this.msj.showError3('Error',err.error);
    });
  }

  submitForm1() {

    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
    this.authService.subefoto('',  'Images', this.foto1img, '' , '' , '', this.form.controls['imagen'].value,'update' ).then((result) => {
      let data: any = [];
      data = result;
      this.form.controls['imagen'].setValue(data.url);

          const   options: any	     = {
            idempresa:this.idempresa,
            nombre : this.form.controls['nombre'].value,
            fechaini : this.form.controls['fechaini'].value,
            fechafin : this.form.controls['fechafin'].value,
            ubicacion : this.form.controls['ubicacion'].value,
            fecha : this.form.controls['fecha'].value,
            costo : this.form.controls['costo'].value,
            foto : this.form.controls['imagen'].value,
            plantilla : this.form.controls['plantilla'].value,
            impresion : this.form.controls['impresion'].value,
            nomax : this.form.controls['nomax'].value,
            unidad: this.unidad,
            bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' evento '}
        };

          if (options) {
            this.authService.createReg(this._ID, options, 'eventos').then((result) => {
              if (!this.form.valid) { return; }

              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });

          }

    }, (err) => {
      this.msj.showError3('Error',err.error);
    });
  }


  manageGallery() {
        if (this.navParams.get('record') == null) {
          this.submitForm0();
        } else {
          this.submitForm1();
        }
    }


  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


  updateProfileImage() {
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph();
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage();
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {

        this.foto1img = image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }


  selectImage() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {

        this.foto1img = image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }

  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
          this.image = readerEvent.target.result;
          this._IMAGE.generateFromImage(this.image, 400, 400, 1, data => {

          this.foto1img = data;
      });
   };
      reader.readAsDataURL(event.target.files[0]);
    }
}
}



